/* global ADL */

import { ActivityBus } from './mixins/Activity.js'
const fb = require('./firebase.js')

ADL.XAPIWrapper.changeConfig({strictCallbacks: true});

const xAPI = {
  setEvents(events) {
    events.forEach(ev => ActivityBus.$on(ev, this.processEvent))
  },
  processEvent: function(vueObject) {
    // eslint-disable-next-line
    console.log(vueObject);
    const stmt = new ADL.XAPIStatement(
      new ADL.XAPIStatement.Agent('mailto:' + fb.auth.currentUser.email, 'email address'),
      new ADL.XAPIStatement.Verb('http://adlnet.gov/expapi/verbs/completed', 'completed'),
      new ADL.XAPIStatement.Activity('https://otaeducation.azurewebsites.net/candlestick-builder', 'candlestick builder activity group')
    );
    stmt.generateId();
    ADL.XAPIWrapper.sendStatement(stmt, (err, resp, obj) => {
      // eslint-disable-next-line
      if (err) { console.log(err) } else { console.log(resp, obj) }
    });
  }
}

export default xAPI;
